import axios from 'axios';
import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Link} from 'react-router-dom'

function Coffin({match}) {
  const [coffins, setCoffin] = useState([])

  useEffect(() => {
    axios({
      method: "GET",
      url: `http://127.0.0.1:8000/api/coffin`
  }).then(response =>{
      setCoffin(response.data)
      })
  },[])

  return (
   <div>
            <table className='table'>
                <thead>
                <tr className="table-warning">
                    <td scope="col">Category form coffin</td>
                    <td scope="col">Coffin name</td>
                    <td scope="col">Image</td>
                    <td scope="col">Coffin description</td>
                    <td scope="col">Package service</td>

                </tr>
                </thead>
            <tbody>
                {coffins.map(coffin => (
                    <tr>
                        <td>{coffin.category_form_coffin}</td>
                        <td>{coffin.coffin_name}</td>
                        <img>{coffin.image}</img>
                        <td>{coffin.coffin_description}</td>
                        <td>{coffin.package_service}</td>

                    </tr>
                ))}
            </tbody>
            </table>
        </div>

  );
}

export default Coffin;